/* eslint-disable react/no-unused-prop-types */
/* eslint-disable @typescript-eslint/no-explicit-any */
import { AtomContainerTypes } from '@atoms/AtomContainer/types';
import AtomModal from '@atoms/AtomModal';
import { AtomWrapperTypes } from '@atoms/AtomWrapper/types';
import { FC, ReactNode, useEffect, useRef } from 'react';

type Props = {
  children: ReactNode;
  isOpen: boolean;
  onClose: () => void;
  componentProps?: {
    containerProps?: AtomContainerTypes;
    wrapperProps?: AtomWrapperTypes;
  };
  key?: string;
};

const AtomModalBox: FC<Props> = (props) => {
  const { isOpen, children, onClose } = props;
  const ref = useRef<HTMLDivElement>(null);
  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      if (ref.current && !ref.current.contains(event.target as Node)) {
        onClose();
      }
    };

    document.addEventListener(`mousedown`, handleClickOutside);
    return () => {
      document.removeEventListener(`mousedown`, handleClickOutside);
    };
  }, [ref]);
  return (
    <AtomModal
      key={props.key ?? ''}
      isOpen={isOpen}
      componentProps={{
        wrapperProps: {
          ...(props?.componentProps?.wrapperProps ?? {}),
          refObject: ref,
        },
        ...(props?.componentProps?.containerProps ?? {}),
      }}
      component={<>{children}</>}
    />
  );
};

export default AtomModalBox;
